<template>
    <div>

        <!--Custom Loader component start-->
        <loader :is-visible="isLoading"></loader>
        <!--Custom Loader component start-->
        <section class="page-action">
            <div class="container-fluid p-0">
                <div class="row">
                    <div class="col-md-12 padding-t_8">
                        <div class="theme-primary partition-full">
                            <span class="p-name text-white">{{ $t("MyShortcuts") }}</span>
                            <span class="p-actions float-end mydbsize">
                                <RouterLink v-if="GetUserInfo.UserType == 'CL'" to="/User/Dashboard"
                                    class="p-action-btn text-white dash" checkPrivilege="Yes">
                                    <em class="fa fa-home"></em><br />{{ $t("Home") }}
                                </RouterLink>
                                <RouterLink v-else to="/Dashboard" class="p-action-btn text-white dash"
                                    checkPrivilege="Yes">
                                    <em class="fa fa-home"></em><br />{{ $t("Home") }}
                                </RouterLink>
                                <a href="javascript:;" @click="AddNewData()" id="ancAddGroupMyDashboard" v-if="IsEdit"
                                    class="p-action-btn text-white " :title="$t('AddNewGroup')"><em class="fa fa-plus"
                                        aria-hidden="true"></em><br>{{ $t("AddNewGroup") }}</a>
                                <a href="javascript:;" @click="SaveData()" id="ancSaveMyDashboard" v-if="IsEdit"
                                    class="p-action-btn text-white" :title="$t('Save')"><em class="fa fa-save"
                                        aria-hidden="true"></em><br>{{ $t("Save")
                                        }}</a>
                                <a href="javascript:;" id="ancCancelMyDashboard" v-if="IsEdit"
                                    @click="CancelEditDashBoardData()" class="p-action-btn text-white"
                                    :title="$t('Cancel')"><em class="fa fa-times" aria-hidden="true"></em><br>{{
                                        $t("Cancel") }}</a>
                                <a href="javascript:;" @click="EditDashBoardData()" v-if="!IsEdit"
                                    class="p-action-btn text-white" checkPrivilege="Yes">
                                    <em class="fa fa-pencil"></em><br />{{ $t("Edit") }}
                                </a>
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <section class="main-content">
            <div class="container-fluid p-0">
                <div class="border p-3 form-minheight d-flex iPadcontroldiv">
                    <div class="float-left leftsideicon" :class="ChangeSizeOfBox == true ? 'w-75' : 'w-100'">
                        <div class="row">
                            <div class="w-100 clsdashboardgroup p-0" v-for="(parentItem, parentIndex) in MenuDataGroupBy"
                                :key="parentIndex">
                                <div class="clearfix"></div>
                                <div class="col-lg-12 mb-0 float-left px-3">
                                    <div class="heading-border" :class="IsEdit == true ? 'editgroup' : ''">
                                        <h5 class="h5">
                                            <span v-if="IsShowModuleNameBorder" class="spngropuname">
                                                {{ parentItem.GroupName }}
                                                <a :title="$t('Edit')" href="javascript:;" class="editgroupname d-none"><em
                                                        class="fa fa-pencil pl-2 text-success"></em></a>
                                            </span>
                                            <input v-if="IsShowModuleNameEdit" type="text" maxlength="100"
                                                class="form-control mb-1 txtgropuname" v-model="parentItem.GroupName" />

                                            <a class="delete-group" v-if="parentIndex > 0 && IsShowModuleNameEdit"
                                                @click="DeleteObjectData(parentItem)"
                                                data-id="@group.First().FAV_DASHBOARD_GROUP_ID" :title="$t('Delete')">
                                                <em class="text-secondary fa fa-times-circle"></em>
                                            </a>

                                        </h5>
                                    </div>

                                </div>




                                <ul class="shortcuts-menu pb-3 connectedSortable">

                                    <draggable v-model="parentItem.GroupData" group="contacts" @end="onDragEnd"
                                        style="width: 100%">

                                        <li class="position-relative" v-for="(item, index) in parentItem.GroupData"
                                            :key="index">
                                            <a v-if="IsEditShow" href="javascript:;" @click="DeleteSelectedData(item)"
                                                :data-id="item.PRIVILEGE_ID" class="delete-dashboard"
                                                :title="$t('Delete')"><em
                                                    class="text-secondary fa fa-times-circle"></em></a>
                                            <RouterLink v-if="getRootLink(item) == 'CURRENT'"
                                                :to="`/${item.MODULE_NAME}/${item.PRIVILEGE_NAME}?reqfrom=${item.MODULE_MASTER_ID}`"
                                                class="short-menu-item" checkPrivilege="Yes">
                                                <span class="left-icon-m"
                                                    :class="IsAddCursorClass == true ? 'cursor-move' : ''">
                                                    <em
                                                        :class="item.PRIVILEGE_ICON ? 'fa ' + item.PRIVILEGE_ICON : 'fa fa-exclamation'"></em>
                                                </span>
                                                <span class="short-item-name">{{ $t(item.DISPLAY_NAME) }}</span>
                                            </RouterLink>
                                            <a v-else
                                                :href="getRootLink(item) + '/' + `${item.MODULE_NAME}/${item.PRIVILEGE_NAME}?reqfrom=${item.MODULE_MASTER_ID}`"
                                                :data-id="item.PRIVILEGE_ID" :data-fav_id="item.FAV_DASHBOARD_ID"
                                                :data-order="item.DISPLAY_ORDER" :data-displayName="item.DISPLAY_NAME"
                                                data-toggle="tooltip" :title="$t(item.DISPLAY_NAME)" data-placement="bottom"
                                                class="short-menu-item">
                                                <span class="left-icon-m"
                                                    :class="IsAddCursorClass == true ? 'cursor-move' : ''">
                                                    <em
                                                        :class="item.PRIVILEGE_ICON ? 'fa ' + item.PRIVILEGE_ICON : 'fa fa-exclamation'"></em>
                                                </span>
                                                <span class="short-item-name">{{ $t(item.DISPLAY_NAME) }}</span>
                                            </a>
                                        </li>
                                    </draggable>

                                </ul>


                            </div>
                        </div>
                    </div>
                    <div v-if="IsShowModuleList" class="w-25 float-left rightsideicon">
                        <div class="left-menu-tab ml-3 mr-3" style="max-width:96%;">
                            <div class="col-md-12 p-0 ">
                                <div class="theme-primary partition">
                                    <span class="p-name text-white">Customize Shortcuts</span>
                                </div>
                            </div>
                            <div class="w-100 p-3 bg-grey common-height">
                                <div class="accordion custom-accr">
                                    <div class="mb-1 w-100 pr-1">
                                        <div class="input-group customsearchbox">
                                            <input type="text" v-model="searchQuery" @input="handleSearch($event)"
                                                :Placeholder="$t('Search')"
                                                class="form-control searchdashboardfilter border-right-0"
                                                autocomplete="off">
                                            <div class="input-group-append"><span
                                                    class="input-group-text bg-transparent bg-white border-left-0"><i
                                                        class="fa fa-search"></i></span></div>
                                        </div>
                                    </div>
                                   

                                    <div class="accordion md-accordion left-filter db_widgetnew" id="accordionEx1"
                                        style="max-height:550px;overflow-y:auto;">
                                        <div class="card-panel">
                                            <div class="card border-0" v-for="(item, index) in moduleList" :key="index">
                                                <div class="card-header p-0" role="tab" :id="'MainFunction' + index">
                                                    <h5 class="mb-0" style="line-height:normal ;"><a data-toggle="collapse"
                                                            data-parent="#accordionEx1" :href="'#FucntionCollapse' + index"
                                                            aria-expanded="true" :aria-controls="'FucntionCollapse' + index"
                                                            class="collapsed">
                                                            <em class="fa fa-angle-down rotate-icon mr-2"
                                                                aria-hidden="true"></em>
                                                            <span class="float-left">
                                                                {{ $t(item.MODULE_DISPLAY_NAME) }}</span></a>
                                                    </h5>
                                                </div>

                                                <div :id="'FucntionCollapse' + index" class="collapse multi-collapse"
                                                    role="tabpanel" :aria-labelledby="'MainFunction' + index"
                                                    data-parent="#accordionEx1">
                                                    <div class="card-body p-0 customScrollForLeftBar llt py-2 pl-2 border ">
                                                        <div class="form-group">
                                                            <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix cursor-move"
                                                                style="max-height:470px; overflow: auto; display: block;">
                                                                <draggable v-model="item.data" group="contacts"
                                                                    @end="onDragEnd(event, item.data)">
                                                                    <li v-for="(contact, Contactindex) in item.data"
                                                                        :key="Contactindex" class="position-relative"><em
                                                                            :class="contact.FAV_DASHBOARD_ID != null && contact.ROLE_APPLICABLE == false ? 'fa fa-check text-success pr-1' : 'fa fa-check text-success pr-1 invisible'"></em>
                                                                        {{ $t(contact.DISPLAY_NAME) }}
                                                                    </li>
                                                                </draggable>
                                                            </ul>

                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
import { storeToRefs } from "pinia";
import DataService from "@/services/DataService"
import { useloggedUserInfo, moduleStore } from "@/stores/UserInfo";
import draggable from "vuedraggable";
const currentAppRootURL = process.env.VUE_APP_CURRENT_SITE_ROOT_URL;
const defaultAppURL = process.env.VUE_APP_DEFAULT_APP_URL;
const fullMenuRedirectionApplied = process.env.VUE_APP_FULL_MENU_APP_REDIRECTION;
export default {
    components: {
        draggable
    },
    data() {
        return {
            MenuData: [],
            MenuDataList: [],
            MaxLoop: 5,
            ModuleName: '',
            MenuDataGroupBy: [],
            MenuCountmodule: 0,
            TotalCount: 0,
            TotalMenuCount: 0,
            Languages: [],
            SelectedLanguage: 'English',
            SelectedMenuType: 'Top',
            IsEdit: false,
            isLoading: false,
            moduleList: [],
            activeAccordion: -1,
            UserType: '',
            searchQuery: '',
            tmpsortedModules: [],
            IsEditShow: false,
            ChangeSizeOfBox: false,
            IsShowModuleList: false,
            IsShowModuleNameBorder: true,
            IsShowModuleNameEdit: false,
            IsAddCursorClass: false,
        }
    },
    setup() {
        const store = useloggedUserInfo();
        const { GetUserInfo } = storeToRefs(store)
        return { GetUserInfo }
    },
    created: function () {

        this.UserType = this.GetUserInfo.UserType;
        const { SetModuleId } = moduleStore();
        SetModuleId(0);
        this.GetFavDashBoardData();
        this.GetModuleListData();
        // this.tmpsortedModules=this.moduleList
        //this.sortedModules=this.moduleList

    },
    // computed: {
    //     sortedModules() {
    //         debugger
    //         return this.moduleList.sort((a, b) => a.MODULE_DISPLAY_NAME.localeCompare(b.MODULE_DISPLAY_NAME));
    //     }
    // },
    methods: {
        // // onDragEnd(event) {
        // //     debugger
        // //     const newIndex = event.newIndex;
        // //     const oldIndex = event.oldIndex;

        // //     if (event.from === event.to) {
        // //         const movedItem = this.item.data.splice(oldIndex, 1)[0];
        // //         this.item.data.splice(newIndex, 0, movedItem);
        // //     } else {
        // //         console.log('Item moved between lists');
        // //     }

        // // },

        onDragEnd: function (e, x) {

            if (e.item.id == 'StopDragfield') {
                return true;
            }
            else {
                e.preventDefault();
                return true;
            }
        },
        handleSearch(evt) {

            var vm = this;

            let searchQuery = vm.searchQuery.toLowerCase();

            const filterList = (list, key) => list.filter(item => item[key].toLowerCase().includes(searchQuery));

            if (evt && (evt.key !== 'Backspace' || evt.key === 'Backspace')) {
                vm.moduleList = filterList(vm.tmpsortedModules, 'MODULE_DISPLAY_NAME');

            } else {
                vm.moduleList = vm.tmpsortedModules;
            }
            // debugger
            // if (!this.searchQuery.trim()) {
            //     this.sortedModules = this.moduleList; // Reset to all modules if search query is empty
            //     return;
            // }
            // const searchTerm = this.searchQuery.toLowerCase();
            // this.sortedModules = this.moduleList.filter(item => {
            //     return item.MODULE_DISPLAY_NAME.toLowerCase().includes(searchTerm);
            // });
        },
        CancelEditDashBoardData() {
            var vm = this;
            vm.IsEdit = false;
            vm.ChangeSizeOfBox = false;
            vm.IsShowModuleList = false;
            vm.IsShowModuleNameBorder = true;
            vm.IsShowModuleNameEdit = false;
            vm.IsAddCursorClass = false;
            vm.IsEditShow = false;
        },
        toggleCollapse(index) {
            if (this.activeAccordion === index) {
                this.activeAccordion = -1; // Close if same accordion is clicked
            } else {
                this.activeAccordion = index; // Open clicked accordion
            }
        },
        filteredGroups(moduleId) {
            // Function to filter groups by moduleId and UserType
            return this.moduleList.filter(grp => grp.MODULE_ID === moduleId && grp.USER_TYPE_NAME === this.UserType);
        },
        // EditDashBoardData() {
        //     var vm = this;
        //     this.IsEdit = true;*
        //     $(".leftsideicon").find(".txtgropuname").closest(".heading-border").addClass("editgroup");
        //     $(".leftsideicon").addClass("w-75").removeClass("w-100");*
        //     $(".rightsideicon").removeClass("d-none");*
        //     $("#ancSaveMyDashboard, #ancCancelMyDashboard, #ancAddGroupMyDashboard").removeClass("d-none");*
        //     $('.spngropuname').attr("contenteditable", true).hide();*
        //     $('.txtgropuname').show();*
        //     $('.spngropuname').off('keypress').on("keypress", function () {
        //         return (this.innerText.length <= 100)
        //     });*
        //     $(".left-icon-m").addClass("cursor-move").off('click').on('click', function () {
        //         return false;
        //     });*
        //     $('.shortcuts-menu a:not(.delete-dashboard)').off('click').on('click', function () {
        //         return false;
        //     });
        //     $('.shortcuts-menu a:not(.delete-group)').off('click').on('click', function () {
        //         return false;
        //     });
        //     vm.IsEditShow = true;
        // },
        EditDashBoardData() {
            var vm = this;
            vm.IsEdit = true;
            vm.ChangeSizeOfBox = true;
            vm.IsShowModuleList = true;
            vm.IsShowModuleNameBorder = false;
            vm.IsShowModuleNameEdit = true;
            vm.IsAddCursorClass = true;
            vm.IsEditShow = true;
        },
        async GetModuleListData() {

            var vm = this;
            vm.isLoading = true;
            DataService.GetCustomizeShortcutMenu()
                .then((response) => {
                    vm.isLoading = false;

                    if (response.data.status == "Success") {
                        vm.moduleList = response.data.result.ListingData;
                        vm.tmpsortedModules = response.data.result.ListingData;
                    }

                    vm.MenuDataGroupBy.forEach((mainItem, indexVal) => {

                        mainItem.GroupData.forEach((itm, indx) => {

                            var ID = itm.PRIVILEGE_ID;

                            vm.moduleList.forEach((ModuleItem, ModuleindexVal) => {

                                ModuleItem.data.forEach((Mitm, Mindx) => {

                                    ModuleItem.data = ModuleItem.data.filter(Mitm => Mitm.PRIVILEGE_ID !== ID);


                                });

                            });


                        });
                    });
                });







        },
        async GetFavDashBoardData() {

            var vm = this;
            vm.isLoading = true;
            DataService.GetFavDashboard()
                .then((response) => {
                    vm.isLoading = false;
                    if (response.data.status == "Success") {
                        vm.MenuData = response.data.result.DATA;
                        var MenuGroupData = [];
                        if (vm.MenuData != null && vm.MenuData.length > 0) {
                            var groupId = vm.MenuData[0].FAV_DASHBOARD_GROUP_ID;
                            $.each(vm.MenuData, $.proxy(function (index, item) {
                                groupId = item.FAV_DASHBOARD_GROUP_ID;
                                var chkGroup = $.grep(MenuGroupData, function (element, index) {
                                    return element.GroupId == groupId;
                                });

                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(vm.MenuData, function (element, index) {
                                        return element.FAV_DASHBOARD_GROUP_ID == groupId;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            GroupId: groupId,
                                            GroupName: returnedData[0].GROUP_NAME,
                                            GroupData: returnedData
                                        };
                                        MenuGroupData.push(objGroup);
                                    }
                                }
                            }, this));
                            vm.MenuDataGroupBy = MenuGroupData;
                        }

                    }
                });
        },
        getRootLink: function (link) {

            var rtnValue = "";
            if (fullMenuRedirectionApplied == "true") {
                if (link) {
                    if (link.ROOT_URL != null && link.ROOT_URL.toLowerCase() == currentAppRootURL.toLowerCase()) {
                        rtnValue = "CURRENT";
                    }
                    else if (link.ROOT_URL != null) {
                        rtnValue = link.ROOT_URL;
                    }
                    else {
                        rtnValue = defaultAppURL;
                    }
                }
            }
            else {
                rtnValue = "CURRENT";
            }
            return rtnValue;
        },
        DeleteSelectedData: function (obj) {
           
            var vm = this;
            vm.confirmR(
                vm.$t("ConfirmModalDelete"),
                true,
                vm.$t("Confirm"),
                function () {

                    let RemoveModuleName = obj.DISPLAY_NAME;

                    vm.MenuDataGroupBy.forEach(mainItem => {

                        mainItem.GroupData = mainItem.GroupData.filter(item => item.DISPLAY_NAME !== RemoveModuleName);
                    });

                    vm.moduleList.forEach((ModuleItem, ModuleindexVal) => {

                        if(ModuleItem.MODULE_DISPLAY_NAME == obj.MODULE_NAME)
                        {
                            ModuleItem.data.push(obj);
                        }
                      
                    });
                }
            )

        },
        SaveData: function () {

            var vm = this;
            let myObj = [];

            vm.MenuDataGroupBy.forEach((mainItem, indexVal) => {
                let groupObj = {
                    GroupIndex: indexVal + 1,
                    GroupName: mainItem.GroupName,
                    Data: []
                };

                mainItem.GroupData.forEach((itm, indx) => {

                    groupObj.Data.push({
                        id: itm.PRIVILEGE_ID,
                        order: indx
                    });
                });

                myObj.push(groupObj);

            });

            var Data = JSON.stringify(myObj);

            DataService.SaveShortcutData(Data).then(function (response) {

                if (response.data != null) {

                    vm.ShowAlert(vm.$t("InterviewStageAdded", { 0: vm.$t("Record") }), "warning", true, vm.$t("Alert"));

                    vm.IsEdit = false;
                    vm.ChangeSizeOfBox = false;
                    vm.IsShowModuleList = false;
                    vm.IsShowModuleNameBorder = true;
                    vm.IsShowModuleNameEdit = false;
                    vm.IsAddCursorClass = false;
                    vm.IsEditShow = false;
                    vm.GetFavDashBoardData();
                    vm.GetModuleListData();
                }

            });

        },
        AddNewData: function () {

            var vm = this;

            this.MenuDataGroupBy.push({ GroupId: 1, GroupName: '', GroupData: [] });
        },
        DeleteObjectData: function (obj) {

            var vm = this;

            let RemoveModuleId = obj.GroupId;

            const index = vm.MenuDataGroupBy.findIndex(item => item.GroupId == RemoveModuleId);
            if (index !== -1) {
                vm.MenuDataGroupBy.splice(index, 1);
            }

            // vm.MenuDataGroupBy.forEach(mainItem => {

            //     mainItem = vm.MenuDataGroupBy.filter(item => item.GroupId !== RemoveModuleId);
            // });
            console.log('vm.MenuDataGroupBy', vm.MenuDataGroupBy)


        }
    }

}

</script>

<style>
.leftsideicon.w-75 .delete-dashboard {
    display: block;
    display: block;
    position: absolute;
    right: 6px;
    top: -2px;
    z-index: 99;
    font-size: 1.5em;
    background: #fff;
    height: 28px;
    border-radius: 100%;
}

.heading-border.editgroup:after {
    background: none !important;
}

.left-filter.db_widgetnew .card-header {
    text-transform: capitalize;
}

.leftsideicon.w-75 .delete-group {
    display: block;
    position: absolute;
    right: 6px;
    top: -2px;
    z-index: 99;
    font-size: 1.5em;
    height: 28px;
}

.db_widgetnew ul.lefttabiconfix li {
    padding: 10px;
}
</style>
